// REBOOT
@import 'styles/utils/reset';
@import 'styles/utils/gobal';

// FRAMEWORKS
// -- MATERIAL
@import 'styles/framework/material/core';
@import 'src/styles/fonts';

/* You can add global styles to this file, and also import other style files */
//@import '~@fullcalendar/core/main.css';
@import '~@fullcalendar/daygrid/main.css';

@import 'css/theme.min.css';

$blue: #2b6cb0;
$blue2: #398ae0;
$white: #fff;
$bg-white: #f5f5f5;

html,
body {
  height: 100%;
  width: 100%;
}
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: $bg-white;
}

/*Snack Bar General Style */
.green-snackbar {
  background: #d4edda;
  color: #155724;
  border-color: #c3e6cb;
}
.green-snackbar button {
  background-color: #d4edda;
  color: #155724;
  border: none;
}
.red-snackbar {
  background: #f8d7da;
  color: #721c24;
  border-color: #f5c6cb;
}
.red-snackbar button {
  background-color: #f8d7da;
  color: #721c24 !important;
  border: none;
}
.yellow-snackbar {
  background: #f8f0d7;
  color: #72521c;
  border-color: #f5e9c6;
}
.yellow-snackbar button {
  background-color: #f8f0d7;
  color: #72521c !important;
  border: none;
}
